/**
 * 
 * 
 * @param {Array} array  
 */
function random(array = []) {
  return array.sort(() => Math.random() - 0.5)
}

/**
 * 
 * 
 * @param {Object} arr
 */
function isArray(arr) {
  return Object.prototype.toString.call(arr) === '[object Array]'
}

export default {
  random,
  isArray
}