/**
 * 
 * @param {String, Number} value 
 * @param {Number} digits  
 */
function formatNumberString(value, digits = 2) {
  let number = 0
  // 
  if (typeof value === 'string') {
    number = Number(value)
  } else if (typeof value === 'number') {
    number = value
  }
  if (isNaN(number) || number === 0) {
    return 0
  }
  
  let maxNumber = Math.pow(10, digits) - 1
  if (number > maxNumber) {
    return `${maxNumber}+`
  }
  
  return number
}

/**
 * 0
 * 
 * @param {Object} num 
 */
function formatNumberAddZero(value) {
  let number = 0
  // 
  if (typeof value === 'string') {
    number = Number(value)
  } else if (typeof value === 'number') {
    number = value
  }
  if (isNaN(number) || +number < 10) {
    return '0' + number
  } else {
    return String(number)
  }
  
}

/**
 * 
 *
 * @param {Object} value 
 * @param {Object} digits 
 */
function formatNumberAddPriceUnit(value, digits = 2) {
  // 
  const unitSplit = [
    { value: 1, symbol: ''},
    { value: 1E3, symbol: 'K'},
    { value: 1E4, symbol: 'W'},
  ]
  
  const reg = /\.0+$|(\.[0=9]*[1-9])0+$/
  
  let number = 0
  // 
  if (typeof value === 'string') {
    number = Number(value)
  } else if (typeof value === 'number') {
    number = value
  }
  
  let i
  for (i = unitSplit.length - 1; i > 0; i--) {
    if (number >= unitSplit[i].value) break
  }
  return (number / unitSplit[i].value).toFixed(digits).replace(reg, "$1") + unitSplit[i].symbol
}

/**
 * 
 *
 * @param {Object} number  
 */
function getDigit(number) {
  let digit = -1
  while (number >= 1) {
    digit++
    number = number / 10
  }
  return digit
}

/**
 * ()
 
 * @param {Object} min 
 * @param {Object} max 
 */
function random(min, max) {
  if (min >= 0 && max > 0 && max >= min) {
    let gab = max - min
    return Math.random() * gab + min
  } else {
    return 0
  }
}

/**
 * ()
 
 * @param {Object} min 
 * @param {Object} max 
 */
function randomInt(min, max) {
  if (min >= 0 && max > 0 && max >= min) {
    let gab = max - min + 1
    return Math.floor(Math.random() * gab + min)
  } else {
    return 0
  }
}

export default {
  formatNumberString,
  formatNumberAddZero,
  formatNumberAddPriceUnit,
  random,
  randomInt
}