export default function silenceUpdate(url) {
	uni.downloadFile({
		url,
		success: res => {
			if (res.statusCode === 200) {
				plus.runtime.install(
					res.tempFilePath, {
						force: true //truefalse
					},
					function() {
						uni.showModal({
							title: '',
							content: '',
							showCancel: false,
							success: function(res) {
								if (res.confirm) {
									// console.log('');
									plus.runtime.restart()
								}
							}
						});
						// console.log('install success...');
					},
					function(e) {
						console.error('install fail...');
					}
				);
			}
		}
	});
}
