import * as common from './common.js'
//
import {
	pingInterval,
	timeout
} from './config.js'

import {
	webSocket
	} from '@/siteinfo.js'


var test = 'ok'
var state = 'fail'
var time = 0;

function connect() {
	let newTime = new Date().getTime() - time;
	// 30
	if (state != 'connect') {
		uni.connectSocket({
			url: webSocket,
			success: (res) => {			
				if (uni.getStorageSync('user_id'))
				{	
					setTimeout(() => {
					bind()	
					}, 1000);
				}
			
			},
			fail: (res) => {
				console.log('', res)
			},
		});
		uni.onSocketOpen(res => {
			state = 'connect';
			console.log('', res);
			if (pingInterval) {
				ping();
			}
			time = new Date().getTime();
			uni.$emit('socketOpen')
		});
		uni.onSocketError(res => {
			state = 'fail'
			uni.$emit('socketError')
			console.log('WebSocket', res);

		});
		uni.onSocketMessage(res => {
		//	console.log('' + res.data);
			let data = JSON.parse(res.data)
			uni.$emit('listMessage',data)
			uni.$emit('talkMessage',data)	
					
			if (data.toid==uni.getStorageSync('user_id')){
				uni.$emit('index',data)	
			}		
			if (data.id==uni.getStorageSync('user_id')){
				uni.$emit('index',data)	
			}			
					
		});
	} else {
		console.log('WebSocket')
	}

}


function bind() {
	let msg = {
		'type': 'bind',
		'fromid': uni.getStorageSync('user_id')
	};
	uni.sendSocketMessage({
		data: JSON.stringify(msg),
		success: (res) => {
			console.log('id' + uni.getStorageSync('user_id'), res)
		},
		fail: (res) => {
			//console.log('id'+uni.getStorageSync('user_id'), res)
		}
	});

}

function loginout() {
	let msg = {
		'type': 'bind',
		'fromid':0
	};
	uni.sendSocketMessage({
		data: JSON.stringify(msg),
		success: (res) => {
			console.log('', res)
		},
		fail: (res) => {
			
		}
	});

}






function ping() {
	uni.sendSocketMessage({
		data: JSON.stringify({
			type: 'ping'
		}),
		fail: (res) => {
			connect()
			console.log('')
		}
		
	});
	setTimeout(() => {
		if (state == 'connect') {
			ping();
		}
	}, pingInterval * 1000);
}


function send(body) {
	uni.sendSocketMessage({
				data: body,
				success: (res) => {					
			console.log('send'+body)				
				},
			});
}




export {
	connect,send,bind,loginout
}
