module.exports = {
  onLoad() {
    // 
    this.$t.mpShare = {
      // 
      title: '',
      // 
      path: '',
      // 
      imageUrl: '',
      // 
      share: true
    }
    if (!this.$t.mpShare.share) {
      uni.hideShareMenu()
    }
  },
  onShareAppMessage() {
    return this.$t.mpShare
  },

  onShareTimeline() {
    return {
      title: this.$t.mpShare.title,
      query: this.$t.mpShare.path.substring(this.$t.mpShare.path.indexOf('?') + 1, this.$t.mpShare.path.length),
      imageUrl: this.$t.mpShare.imageUrl
    }
  }

}