// provide/inject
// H5this.$parentH5this.$parent.$parent.xxx
// undefined$parent
export default function $parent(name = undefined) {
  let parent = this.$parent
  // whleH5
  while(parent) {
    // 
    if (parent.$options && parent.$options.name !== name) {
      // name
      parent = parent.$parent
    } else {
      return parent
    }
  }
  
  return false
}